.footer{
    position: fixed;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px 0;
    background: #2E2E2E;
    color: white;
    flex-wrap: wrap;
    z-index: 999;
}
.footer__block{
    & + .footer__block{
        margin-left: 70px;
    }
}
.footer__text{
    font-size: 15px;
    span{
        font-family: 'Montserrat', sans-serif;
        font-weight: 600;
        font-size: 16px;
    }
}
.footer__address{
    display: flex;
    align-items: center;
    .fa{
        color: $primaryColor;
        font-size: 20px;
        margin-right: 15px;
    }
}
@media all and(max-width:425px){
    .footer{
        flex-direction: column;
    }
    .footer__block{
        & + .footer__block{
            margin-left: 0;
            margin-top: 10;
        }
    }
}