html{
    height: auto;
}
body{
    color:#222;
    font-family: 'Open Sans', sans-serif;
    &.no_overflow{
        overflow: hidden;
    }
}
.hidden{
    opacity: 0;
}
.visible{
    opacity: 1;
}
.container{
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 15px;
}
.welcome{
    height: 100vh;
    background: url(../img/welcome.jpg) no-repeat center center;
    background-size: cover;
    position: relative;
    display: flex;
    padding: 100px 50px;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    *{
        color:white;
    }
}
.welcome__filter{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    background-color:rgba(#000000,0.6);
    z-index: 0;
}
.welcome__text{
    z-index: 1;
    font-family: 'OpenSansRegular';
    text-align: center;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    height: 100%;
    .logo{
        font-size: 60px;
        font-family: 'MontserratRegular';
        letter-spacing: 1.3px;
        line-height: 1.2;
    }
    .sub-logo{
        font-size: 24px;
        letter-spacing: 1.1px;
        p{
            margin-bottom: 15px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
}
.section{
    padding: 70px 0;
    background-color:#fff;
    &:nth-child(even){
        background-color:rgba(#000000,0.05);
    }
    &-instagram{
        padding: 0;
    }
}
.section__title{
    text-align:center;
    text-transform: uppercase;
    font-size: 34px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
}
.section__subTitle{
    font-size: 16px;
    margin-top: 20px;
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
}
.section__row{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
}
.section__column{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    flex:1 1 50%;
}
.section__content{
    margin-top: 60px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    .section__column{
        margin-bottom: 15px;
    }
}
.section__img{
    img{
        max-width:500px;
        height: 100%;
        max-height: 500px;
        object-fit: cover;
    }
}
.btn__wrapper{
    margin: 0 auto;
    margin-top: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 300px;
    height: 50px;
    border:2px solid $primaryColor;
    cursor: pointer;
    overflow: hidden;
    &.left-right{
        &::before{
            content:'';
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            z-index: 0;
            transform: translate(-100%,0);
            background:$primaryColor;
            height: 100%;
            transition: 0.3s all ease-in-out;
        }
        &:hover{
            &::before{
                transform: translate(0,0);
            }
            .btn{
                color:#Fff;
            }
        }
    }
    &.right-left{
        &::after{
            content:'';
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            z-index: 0;
            transform: translate(100%,0);
            background:$primaryColor;
            height: 100%;
            transition: 0.3s all ease-in-out;
        }
        &:hover{
            &::after{
                transform: translate(0,0);
            }
            .btn{
                color:#Fff;
            }
        }
    }
}
.btn{
    z-index: 1;
    font-family: 'Open Sans', sans-serif;
    color:$textColor;
    font-weight: 700;
    font-size: 18px;
    letter-spacing: 1.1px;
    text-transform: uppercase;
    transition: 0.3s all ease-in-out;
}
.instragram-slider__img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.btn-section{
    padding:30px 0;
    .btn__wrapper{
        margin-top: 30px;
    }
    .section__content{
        margin-top: 30px;
    }
}
@media all and (max-width:768px){
    .section__img{
        img{
            max-width: 300px;
        }
    }
    .section__column{
        width: 100%;
    }
    .section__content{
        flex-direction: column;
        >div + div{
            margin-top: 30px;
        }
    }
    .btn-section{
        .section__content{
            >div + div{
                margin-top: 0;
            }
        }
    }
    .welcome__text{
        .sub-logo{
            font-size: 18px;
        }
    }
}
.map{
    width: 100%;
    height: 500px;
    padding-bottom: 47px;
}