.prices{
    width: 100%;
    padding: 0 50px;
}
.prices__title{
    text-align: center;
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
}
.prices__subTitle{
    font-size: 14px;
    margin:10px 0;
    font-weight: 700;
    text-align: center;
    color:#808080;
}
.prices__table{
    width: 100%;
}
.prices__block + .prices__block{
    margin-top: 30px;
}
.prices__row{
    display: flex;
    width: 100%;
    border:1px solid rgba($accentColor,0.8);
    border-radius: 4px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    & + .prices__row{
        margin-top: 5px;     
    }
}  
.prices__column{
    flex: 1 1 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 35px;
    p{
        text-align: center;
    }
    .price{
        font-weight: 600;
        font-size: 16px;
    }
    &:last-child{
        flex:1 1 30%;
        border-left: 1px solid $accentColor;
    }
}
@media all and (max-width:425px){
    .prices{
        padding: 0 15px;
    }
}