.section-review{
    .btn__wrapper{
        margin-top: 30px;
    }
}
.reviews{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    .more_link{
        margin: 30px 0;
    }
}
.more_link{
    a{
        font-size: 17px;
        color: $primaryColor;
        transition:.3s all ease-in-out;
        font-weight: 600;
        font-family:'Montserrat';
        &:hover{
            color:$accentColor;
        }
    }
}
.img__wrapper{
    width: 100%;
    height: 100%;
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
.reviews__list{
    >li + li{
        margin-top: 25px;
    }
}
.reviews__item{
    display: flex;
    align-items: center;
    padding: 0 50px;
    >div + div{
        margin-left: 10px;
    }
}
.reviews__info{
    display: flex;
    align-items: center;
    justify-content: space-between;   
    font-weight: 700;
    font-family: 'Montserrat',sans-serif;
    .date-time{
        font-style: italic;
        color:#808080;
    }
}
.reviews__content{
    >div + div{
        margin-top: 10px;
    }
}
.reviews__desc{
    font-weight: 600;
    font-size: 15px;   
}
.reviews__mark{
    font-family: 'Montserrat',sans-serif;
    font-weight: 700;
    span{
        font-weight: 700;
        color:green;
    }
}
.reviews__img{
    img{
        height: 64px;
        width: 64px;
        object-fit: cover;
        border-radius: 50%;
    }
}
.reviews__sumary{
    font-size: 15px;
    font-weight: 700;
    span{
        font-family: 'Montserrat', sans-serif;
    }
}
@media all and (max-width:425px){
    .reviews__item{
        padding: 0 15px;
    }
}