.contact{
    width: 100%;
    padding:0 50px;
}
.contact__block{
    display: flex;
    & + .contact__block{
        margin-top: 30px;
    }
    >div+div{
        margin-left: 15px;
    }
}
.contact__img{
    .fa{
        font-size: 30px;
    }
}
.contact__content{
    font-size: 15px;
    font-weight: 600;
    p{
        text-align: justify;
    }
}
.phone-list__item{
    &+.phone-list__item{
        margin-top:7px;
    }
    a{
        color:$textColor;
        text-decoration: underline;
    }
}
.operating_mode{
    p + p{
        margin-top:5px;
    }
    span{
        color:$textColor;
    }
    .week-day{
        font-weight: 700;
    }
    .number{
        font-family: 'Montserrat', sans-serif;
        font-weight: 700;
    }
}
.social-list{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top:30px;
}
.social-list__item{
    & + .social-list__item{
        margin-left: 30px;
    }
    .fa{
        font-size:36px;
        transition:0.3s all ease-in-out;
    }
}
.fa{
    color:$textColor;
    &-telegram{
        &:hover{
            color:#2096D4;
        }
    }
    &-instagram{
        &:hover{
            color:#8A41D0;
        }
    }
    &-facebook-official{
        &:hover{
            color:#1D4D99;
        }
    }
}

