.photo{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}
.photo__item{
    flex: 1 1 30%;
    text-align: center;
    margin-bottom:30px;
    &+.photo__item{
        margin-left: 15px;
    }
    >div + div{
        margin-top: 15px;
    }
}
.photo__title{
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 16px;
    text-transform: uppercase;
}
.photo__desc{
    color:#808080;
    font-weight: 700;
    font-size: 13px;
}
.photo__img{
    position: relative;
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &::after{
        content:'';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 3;
        background:rgba(0,0,0,0.3);
        opacity: 0;
        transition:0.3s all ease-in-out;
    }
    &:hover{
        &:after{
            opacity: 1;
        }
    }
}
@media all and(max-width:768px){
    .photo__item{
        flex: 1 1 45%;
    }
}