.advantages{
    display: flex;
    margin-bottom: 30px;
    &:last-child{
        margin-bottom: 0;
    }
}
.advantages__img{
    margin-right: 30px;
}
.advantages__title{
    text-transform: uppercase;
    font-family: 'Montserrat',sans-serif;
    font-weight: 600;
    font-size: 16px;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    .fa{
        font-size: 30px;
        margin-right: 15px;
    }
}
.advantages__img{
    font-size: 30px;
    color:$grey;
}
.lvl-two__item{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
    font-size: 15px;
    margin-bottom: 5px;
    color:#2E2C39;
    &:last-child{
        margin-bottom: 0;
    }
    &::before{
        margin-right: 10px;
        content: '\f00c ';
        font-family: 'FontAwesome';
        color: $primaryColor;
    }
}
.advantages{
    width: 100%;
    align-items: flex-start;
    justify-content: center;
}
.advantages__text{
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
    font-size: 16px;
    margin-bottom: 10px;
    color:$grey;
}
.advantages-list{
    .cirlce_style{
        .lvl-two__item{
            &::before{
                content: '\f0c8';
                color: $primaryColor;
                margin-right: 15px;
            }
        }
    }
    .lvl-two__item{
        & + .lvl-two__item{
            margin-top: 15px;
        }
    }
}