.form-feedback{
    width: 100%;
}
.form-feedback__row{
    & + .form-feedback__row{
        margin-top: 15px;
    }
    display: flex;
    >div{
        flex: 1 1;
    }
    .btn__wrapper{
        margin-top:15px;
        &.right-left{
            &::after{
                transform: translate(0,100%);
            }
            &:hover{
                &::after{
                    transform: translate(0,0);
                }
            }
        }
    }
}
.form-feedback__input{
    display: flex;
    flex-direction: column;
    &+.form-feedback__input{
        margin-left: 10px;
    }
    label{
        margin-bottom: 10px;
        font-family: 'Open Sans', sans-serif;
        font-weight: 600;
    }
    input,select,option{
        height: 40px;
        padding:0 15px;
        font-weight: 500;
        font-family: 'Montserrat', sans-serif;
        font-size: 15px;
    }
    input,select{
        box-shadow: 0 1px 2px 1px rgba(0,0,0,0.15);
    }
}
@media all and(max-width:490px){
 .form-feedback__row{
        flex-direction: column;
        .btn__wrapper{
            flex:unset;
        }
    } 
    .form-feedback__input{
        & + .form-feedback__input{
            margin: 0;
            margin-top:15px;
        }
    }  
}