.main-nav{
    height: 70px;
    border-bottom: 1px solid #ddd;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px 5px -3px #ececec;
    &.stickytop{
        position: sticky;
        top: 0;
        z-index: 1000;
        background:white;
    }
    &.mobile{
        padding: 0 15px;
        .main-nav__list{
            &.main-nav__lvl-one{
            position: fixed;
            top: 0;
            left: 0;
            bottom:0;
            right: 0;
            background-color: rgba(0,0,0,0.95);
            z-index: 1000;
            flex-direction: column;
            transform: translate(-100%,0);
            transition:0.3s all ease-in-out;
            opacity: 0;
            }
        }
        .main-nav__item{
            &-close{
                display: block;
                position: absolute;
                top: 30px;
                right: 30px;
                .fa{
                    font-size: 28px;
                    transition:0.3s all ease-in-out;
                }
                &:hover{
                    .fa{
                        color:#800000;
                    }
                }
            }
        }
        .main-nav__link{
            color:white;
        }
        .main-nav__logo{
            display: none;
        }
        .bar{
            display: block;
            cursor: pointer;
            &:hover{
                .bar__line{
                    transition:0.3s all ease-in-out;
                    background-color:$primaryColor;
                }
            }
        }
        &.active{
            .main-nav__lvl-one{
                opacity: 1;
                transform: translate(0,0);
                display: flex;
            }
        }
    }
    &.descktop{
        .main-nav__item{
            margin: 0 25px;
            position: relative;
            &-close{
                display: none;
            }
            &:hover{
                >.main-nav__lvl-two{
                    opacity: 1;
                }
            }
        }
    }
}
.bar{
    display: none;
}
.bar__wrapper{
    height: 30px;
    width: 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.bar__line{
    height: 5px;
    width: 100%;
    background-color:black;
}
.main-nav__list{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 15px;
    >.main-nav__item-one{
        height: 70px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
.main-nav__lvl-two{
    opacity: 0;
    position: absolute;
    display: flex;
    flex-direction: column;
    left: 0;
    border: 1px solid #ddd;
    top: 69px;
    padding: 15px 30px;
    z-index: 2;
    background-color: #fff;
    .main-nav__item{
        margin-bottom: 15px;
        &:last-child{
            margin-bottom: 0;
        }
    }
}
.main-nav__link{
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    color:$textColor;
    font-size: 15px;
    transition: 0.3s all ease-in-out;
    &:hover{
        color:$primaryColor
    }
}
.main-nav__logo{
    margin: 0 80px;
    img{
        height: 50px;
    }
}
@media all and (max-width:1024px){
    .main-nav__logo{
        margin:0 40px;
    }
}
@media all and (max-width:920px){
    .main-nav__log{
        margin: 0 20px;
    }
    .main-nav.descktop{
        .main-nav__item{
            &-one{
                margin: 0 15px;
            }
        }
    }
}